<script>
  import Steps from 'src/components/Steps/'
  import Step from 'src/components/Steps/Step.vue'

  export default {
    data () {
      return {
        current: 0
      }
    },
    components: {
      Steps,
      Step
    },
    methods: {
      _next () {
        if(this.current === 3) {
          this.current = 0;
          return
        }
        this.current++;
      }
    }
  }
</script>

