<template>
  <div class="bs-docs-section" id="alert">
    <h3 class="page-header">
      <a href="#alert" class="anchor">Alert </a>
    </h3>
    <div class="bs-example" >
      <button class="btn btn-success btn-lg"
        @click="showRight = !showRight">
        
      </button>

      <button class="btn btn-danger btn-lg"
        @click="showTop = !showTop">
        
      </button>
      <hr>
      <alert type="success" >
        
      </alert>

      <alert type="info" >
        
      </alert>

      <alert type="danger" dismissable>
        ,
      </alert>

      <alert type="warning" dismissable>
        ,
      </alert>

      <alert type="help" dismissable>
        ,
      </alert>

      <alert
        :show.sync="showRight"
        :duration="3000"
        type="success"
        width="400px"
        placement="top-right"
        dismissable
      >
        <span class="icon-ok-circled alert-icon-float-left"></span>
        <p></p>
      </alert>

      <alert
        :show.sync="showTop"
        :duration="3000"
        type="danger"
        width="400px"
        placement="top"
        dismissable>
        <span class="icon-info-circled alert-icon-float-left"></span>
        <p></p>
      </alert>
    </div>

    <pre><code class="language-markup"><script type="language-mark-up">
<button class="btn btn-default btn-lg"
  @click="showRight = !showRight">
  

<button class="btn btn-default btn-lg"
  @click="showTop = !showTop">
  
</button>
<hr>
<alert type="success" >
  
</alert>

<alert type="info" >
  
</alert>

<alert type="danger" dismissable>
  
</alert>

<alert type="warning" dismissable>
  
</alert>

<alert type="help" dismissable>
  
</alert>

<alert
  show="{{showRight}}"
  duration="3000"
  type="success"
  width="400px"
  placement="top-right"
  dismissable>
  <span class="icon-ok-circled alert-icon-float-left"></span>
  <strong>Well Done!</strong>
  <p>.</p>
</alert>

<alert
  show="{{showTop}}"
  duration="3000"
  type="info"
  width="400px"
  placement="top"
  dismissable>
  <span class="icon-info-circled alert-icon-float-left"></span>
  <strong>Heads up!</strong>
  <p></p>
</alert>

</script></code></pre>

    <h2>Options</h2>
    <table class="table table-bordered">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>show</td>
          <td><code>Boolean</code></td>
          <td><code>true</code></td>
          <td></td>
        </tr>
        <tr>
          <td>dismissable</td>
          <td><code>Boolean</code></td>
          <td><code>false</code></td>
          <td></td>
        </tr>
        <tr>
          <td>type</td>
          <td><code>String</code>, one of
          <code>success</code>
          <code>info</code>
          <code>warning</code>
          <code>danger</code>
          <code>help</code>
          </td>
          <td><code>success</code></td>
          <td></td>
        </tr>
        <tr>
          <td>duration</td>
          <td><code>Number</code></td>
          <td><code>0</code></td>
          <td>.  <code>0</code>.</td>
        </tr>
        <tr>
          <td>width</td>
          <td><code>String</code></td>
          <td></td>
        </tr>
        <tr>
          <td>placement</td>
          <td><code>String</code>. one of <code>top</code>, <code>top-right</code></td>
          <td></td>
          <td></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import alert from 'src/components/Alert.vue'
  export default {
    data() {
      return {
        showRight: false,
        showTop: false
      }
    },
    components: {
      alert,
    }
  }
</script>

<style>
  .alert-icon-float-left {
    font-size:32px;float:left;margin-right:5px;
  }
</style>



